.thanks-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 85vh;
  background-image: url("/images/thankyoubg.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 100%;

  img {
    justify-content: center;
    align-items: center;
    left: 20%;
    position: absolute;
    width: 20%;

    @media screen and (max-width: 768px) {
      left: -5%;
      width: 40%;
    }
  }

  .thanks-section {
    margin-top: -5em;
    width: 40em;
    padding-bottom: 2em;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);

    @media screen and (max-width: 600px) {
      margin-top: 3em;

      h2 {
        font-size: 20px;
      }
    }

    .header {
      font-size: 25px;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      background-color: #10a3ff;
      padding: 0.8em;
      color: white;
      font-family: $helvetica-bold;

      @media screen and (max-width: 600px) {
        font-size: 18px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        background-color: #10a3ff;
        padding: 0.3em;
        color: white;
        font-family: "Helvetica Bold";
      }
    }
    .thanks-content {
      .caption {
        font-size: 16px;
        font-family: $regular-helvetica;
        line-height: 1.8;
        padding: 2em 0;

        @media screen and (max-width: 600px) {
          font-size: 14px;
          font-family: "Helvetica Regular";
          line-height: 1.4;
          padding: 0em 4em;
        }
      }
    }
  }
}
