﻿.sidebar {
  color: #fff;
  background-color: white;
  box-shadow: 3px 3px 12px rgba(0, 0, 0, 0.1);
  width: 250px;
  max-width: 250px;
  height: 100%;
  float: left;
  position: relative;
  z-index: 1000;
  display: block;
  -webkit-transition: margin 1s;
  transition: margin 1s;
  flex: 1;
  padding-top: 3em;
  overflow-y: scroll;
  height: 89vh;

  h5 {
    font-weight: bold;
    margin-top: 15px;
    color: $studyblue;
  }

  h6 {
    color: black;
  }

  .dropdown-toggle::after {
    display: none;
  }

  .dropdown {
    button {
      background-color: transparent;
      border: none;
      outline: none;
      width: 100%;
      padding: 0.9em 4.9em;
      text-align: left;
      font-size: 12px;
      color: #8e8e8e;

      &:focus {
        outline: none;
        box-shadow: none;
        border: none;
      }

      img {
        padding: 0 1em;
      }
    }
  }

  .dropdown-item {
    position: relative;

    &:hover {
      background-color: $studyblue;
      //color: white !important;
    }
  }
}

#navbar-toggle {
  cursor: pointer;
}

#toggleView {
  margin-left: 44px;
}

.menu-icon {
  float: right;
}

.sidebar-nav {
  display: block;
  float: left;
  width: 100%;
  list-style: none;
  margin: 0;
  padding: 0;
}

.sidebar-nav li a {
  padding-left: 20px;
  font-size: 16px;
  text-decoration: none;
  color: black;
  float: left;
  text-decoration: none;
  width: 100%;
  height: 70px;
  line-height: 25px;
  padding: 20px;
  vertical-align: center;
}

.sidebar-nav li a:hover {
  cursor: pointer;
  -webkit-transition: background 0.5s;
  transition: background 0.5s;
}
.logout-text {
  font-size: 14px;
  color: #002984;
  font-weight: bolder;
  margin-left: 20px;
}

.log-out {
  z-index: 200;
  position: absolute;
  right: 0;
  padding-bottom: 15px;
}

.selected-profile {
  margin-bottom: 20px;
  img {
    width: 35%;
    border-radius: 50%;
  }
}

.custom-carousel {
  @supports (display: grid) {
    .grid-container {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto 1fr auto;
      grid-template-areas: "header header header" "title title footer" "main main main";
    }
    @media screen and (max-width: 500px) {
      .grid-container {
        grid-template-columns: 1fr;
        grid-template-rows: 0.3fr 1fr auto 1fr;
        grid-template-areas: "header" "title" "main" "footer";
      }
    }

    .grid-item {
      color: #fff;
      padding: 3.5em 1em;
      font-size: 1em;
      font-weight: 700;
    }

    .header {
      grid-area: header;
      padding: 1em;
    }

    .title {
      color: #555;
      grid-area: title;
    }

    .main {
      color: #959595;
      grid-area: main;
      padding: 0;
      overflow-x: scroll;
      overflow-y: hidden;
    }

    .items {
      position: relative;
      width: 100%;
      overflow-x: scroll;
      overflow-y: hidden;
      white-space: nowrap;
      transition: all 0.2s;
      transform: scale(0.98);
      will-change: transform;
      user-select: none;
      cursor: pointer;
    }

    .items.active {
      background: rgba(255, 255, 255, 0.3);
      cursor: grabbing;
      cursor: -webkit-grabbing;
      transform: scale(1);
    }

    .item {
      display: inline-block;
      min-width: 400px;
      margin: 0em 0.5em;
    }
    @media screen and (max-width: 500px) {
      .item {
        min-height: 200px;
        min-width: 200px;
      }
    }
  }
  a {
    display: block;
    color: #c9e9f6;
    text-decoration: underline;
    margin: 1em auto;
  }
  a:hover {
    cursor: pointer;
  }

  p {
    text-align: left;
    text-indent: 20px;
    font-weight: 100;
  }

  i {
    color: skyblue;
  }
  .items::-webkit-scrollbar-track,
  .items::-webkit-scrollbar,
  .items::-webkit-scrollbar-thumb {
    width: 0 !important;
    display: none;
  }
  .items {
    scrollbar-width: none !important;
    -ms-overflow-style: none !important;
  }
}
