.carousel-component {
  @supports (display: grid) {
    .grid-container {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto 1fr auto;
      grid-template-areas: "header header header" "title title footer" "main main main";
    }
    @media screen and (max-width: 500px) {
      .grid-container {
        grid-template-columns: 1fr;
        grid-template-rows: 0.3fr 1fr auto 1fr;
        grid-template-areas: "header" "title" "main" "footer";
      }
    }

    .grid-item {
      color: #fff;
      padding: 3.5em 1em;
      font-size: 1em;
      font-weight: 700;
    }

    .header {
      grid-area: header;
      padding: 1em;
    }

    .title {
      color: #555;
      grid-area: title;
    }

    .main {
      color: #959595;
      grid-area: main;
      padding: 0;
      overflow-x: scroll;
      overflow-y: hidden;

      &::-webkit-scrollbar {
        display: none;
      }
    }

    .items {
      position: relative;
      width: 100%;
      overflow-x: scroll;
      overflow-y: hidden;
      white-space: nowrap;
      transition: all 0.2s;
      transform: scale(0.98);
      will-change: transform;
      user-select: none;
      cursor: pointer;
    }

    .items.active {
      background: rgba(255, 255, 255, 0.3);
      cursor: grabbing;
      cursor: -webkit-grabbing;
      transform: scale(1);
    }

    .item {
      display: inline-block;
      min-width: 400px;
    }
    @media screen and (max-width: 500px) {
      .item {
        min-height: 200px;
        min-width: 200px;
      }
    }
  }
  a {
    display: block;
    color: #c9e9f6;
    text-decoration: underline;
    margin: 1em auto;
  }
  a:hover {
    cursor: pointer;
  }

  p {
    text-align: left;
    text-indent: 20px;
    font-weight: 100;
  }

  i {
    color: skyblue;
  }
  .items::-webkit-scrollbar-track,
  .items::-webkit-scrollbar,
  .items::-webkit-scrollbar-thumb {
    width: 0 !important;
    display: none;
  }
  .items {
    scrollbar-width: none !important;
    -ms-overflow-style: none !important;
  }
}

