﻿.quizContainer {
  position: relative;
  margin: 0px auto;
  background-color: #fafafa;
  // background-image: url("../../img/bg-home.svg");
  // background-position: center;
  // background-repeat: no-repeat;
  // background-size: cover;
  position: relative;
  min-height: 100%;
  height: 88vh;
  overflow-y: scroll;
  overflow-x: hidden;
  font-family: "Open Sans" !important;
}
.quizQuestion {
  padding-top: 2em;
  font-size: 28px;
  font-weight: bolder;
  font-family: $helvetica-bold;
  color: black;
  @media screen and (max-width: 768px) {
    font-size: 20px;
    margin-top: 1em;
  }
  @media screen and (min-width: 1400px) {
    font-size: 32px;
  }

  .img-full {
    max-height: 200px;
  }

  .image-full {
    max-height: 200px;
  }
  .image-inline {
    display: inline;
    position: relative;
  }
}

.optionSection {
  padding-top: 3em;
  padding-bottom: 2em;

  @media screen and (max-width: 600px) {
    padding-top: 1.5em;
  }
}

// .optionSection .contain .checkmark::after {
