.optionSection label {
  padding: 1em 0;

  @media screen and (max-width: 600px) {
    padding: 0.6em 0;
  }
}

.col-lg-11 {
  .optionSection label span.question {
    position: relative;
    display: inline-block;
    padding-left: 3%;
    bottom: 15px;
    font-size: 15px;
    font-family: $sans !important;
    @media screen and (max-width: 768px) {
      bottom: 20px;
      padding-left: 7%;
    }
    @media screen and (min-width: 1400px) {
      font-size: 18px;
    }
  }
}

.col-lg-6 {
  .optionSection label span.question {
    position: relative;
    display: inline-block;
    padding-left: 10%;
    bottom: 15px;
    font-family: $sans !important;
    font-size: 15px;
    @media screen and (min-width: 1400px) {
      font-size: 18px;
    }
    @media screen and (max-width: 768px) {
      bottom: 20px;
      padding-left: 7%;
    }
  }
}

.optionSection .checkmark {
  height: 40px;
  width: 40px;
  @media screen and (max-width: 768px) {
    height: 20px;
    width: 20px;
  }
}

