.next-button {
  border-radius: 6px;
  font-family: $helvetica-bold;
  padding-left: 50px;
  padding-top: 15px;
  padding-right: 50px;
  padding-bottom: 15px;
  background-color: #10a3ff;
  border: none;
  color: #fff;
  font-size: 14px;
  margin-right: 15px;
  margin-bottom: 10px;
  @media screen and (max-width: 768px) {
    padding-left: 17px;
    padding-right: 17px;
  }
}

.finish-button {
  border-radius: 6px;
  font-family: $regular-helvetica;
  padding-left: 50px;
  padding-top: 15px;
  padding-right: 50px;
  padding-bottom: 15px;
  background-color: #10a3ff;
  border: none;
  color: #fff;
  font-size: 16px;
  margin-right: 15px;
  margin-bottom: 10px;
  @media screen and (max-width: 768px) {
    padding-left: 17px;
    padding-right: 17px;
  }
}

.done-button {
  border-radius: 6px;
  font-family: $helvetica-bold;
  padding-left: 50px;
  padding-top: 15px;
  padding-right: 50px;
  padding-bottom: 15px;
  background-color: #10a3ff;
  border: none;
  color: #fff;
  font-size: 14px;
  margin-right: 15px;
  @media screen and (max-width: 768px) {
    padding: 15px 25px;
    margin-top: 5px;
  }
}
.done-button:focus {
  outline: none !important;
  border: none;
}
.viewpassage-btn {
  color: $studyblue;
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 15px;
  font-weight: bolder;
  position: absolute;
  z-index: 999;
  cursor: pointer;
  left: 11%;
  top: -3%;
}

.viewpassage-btn:focus {
  outline: none !important;
  border: none;
}

.viewsection-btn {
  color: $studyblue;
  background-color: transparent;
  border-radius: 30px;
  padding: 0.5em;
  outline: none;
  font-size: 13px;
  font-weight: bolder;
  position: absolute;
  z-index: 999;
  cursor: pointer;
  top: 3%;
  right: 15%;
  border: 3px solid $studyblue;
}

.viewsection-btn1 {
  color: $studyblue;
  background-color: transparent;
  outline: none;
  font-size: 15px;
  font-weight: bolder;
  position: absolute;
  z-index: 999;
  cursor: pointer;
  top: -3%;
  right: 15%;
  border: none;
}

.viewsection-btn:focus {
  outline: none !important;
}

.previous-button {
  font-family: $helvetica-bold;
  border-radius: 6px;
  padding-left: 50px;
  padding-top: 15px;
  padding-right: 50px;
  padding-bottom: 15px;
  background-color: transparent;
  border: 1px solid #1e1e1e;
  color: $studyblue;
  font-size: 14px;
  margin-right: 15px;
  margin-bottom: 10px;
  @media screen and (max-width: 768px) {
    padding-left: 17px;
    padding-right: 17px;
  }
}

.next-button:focus {
  outline: none;
}
.previous-button:focus {
  outline: none;
}

.section-bottom {
  //position: fixed;
  z-index: 1001;
  bottom: 0;
  left: 0;
  color: black;
  margin: 0px auto;
  width: 100%;
  //height: 90vh;
  transition: 0.5s;

  .containerr {
    padding: 3em;
    margin-top: 3em;
    font-size: 13px;
    color: black;
    text-align: justify;
    line-height: 2;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    //height: 82vh;
    @media screen and (max-width: 768px) {
      margin-top: 3em;
      height: auto;
      padding: 0;
    }
  }

  @media screen and (max-width: 768px) {
    position: relative;
    height: auto;
  }
}

.section-hide {
  bottom: -5% !important;
}
.section-reset {
  bottom: 0 !important;
}

.decision-style_ {
  position: absolute;
  left: 0;
  z-index: 99999999;

  @media screen and (min-width: 768px) {
    left: 55%;
    bottom: -46%;
  }

  .decision-bg {
    background-color: white;
    padding: 1em;
    width: 100%;
    min-width: 400px;
  }
}

.section_view {
  width: 48%;
  margin-right: 1%;
  margin-left: 1%;
  max-width: 300px;
  border-radius: 6px;
  background-color: transparent;
  border: 1px solid #10a3ff;
  color: #10a3ff;
  font-size: 15px;
  font-weight: bolder;
  padding: 0.7em 1.2em;
  // position: absolute;
  // bottom: 18%;
  // left: 17%;

  @media screen and (max-width: 768px) {
    bottom: 5%;
  }
}
.section_view:focus {
  outline: none !important;
}

.passage_view {
  width: 100%;
  top: -10px;
  position: relative;
  border-radius: 6px;
  margin: 0 auto;
  max-width: 300px;
  background-color: transparent;
  border: 1px solid #10a3ff;
  color: white;
  background-color: #10a3ff;
  font-size: 15px;
  font-weight: bolder;
  padding: 0.7em 1.2em;
}
.passage_view:focus {
  outline: none !important;
}
.wrapper-psg {
  background-color: white;
  max-height: 450px;
  min-height: 400px;
  bottom: 54px;
  position: relative;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 60px;
  overflow-y: scroll;
  font-size: 15px;

  @media screen and (max-width: 768px) {
    position: absolute;
    bottom: 90%;
    width: 100vw;
    margin-left: -15px;
    padding-bottom: 60px;
    bottom: -7px;
    height: 72vh;
    border-top-right-radius: 40px;
    border-top-left-radius: 40px;
    max-height: 530px;
  }

  div {
    div {
      padding-top: 20px;
      font-size: 18px;
      line-height: 1.8;
    }
  }
}
.wrapper-img {
  background-color: white;
  max-height: 450px;
  min-height: 400px;
  bottom: 54px;
  position: relative;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 60px;
  overflow-y: scroll;
  font-size: 15px;

  @media screen and (max-width: 768px) {
    position: absolute;
    bottom: 90%;
    width: 100vw;
    margin-left: -15px;
    padding-bottom: 60px;
    bottom: -7px;
    height: 72vh;
    border-top-right-radius: 40px;
    border-top-left-radius: 40px;
    max-height: 530px;
  }
  div {
    div {
      padding-top: 20px;
    }
  }
}

.bg-white {
  background-color: white;
}
.bg-transparent {
  background-color: transparent;
}

.increase-zIndex {
  @media screen and (max-width: 768px) {
    z-index: 9999;
  }
}
.reduce-zIndex {
  @media screen and (max-width: 768px) {
    z-index: 0;
  }
}

.button-wrapper {
  background-color: white;
  padding: 2em;
  bottom: -4%;
  left: 0%;
  position: absolute;
  width: 100%;

  @media screen and (max-width: 768px) {
    width: 100%;
    position: relative;
    left: 0;
    z-index: 999;
    padding-left: 15px;
  }
  @media screen and (min-width: 1100px) and (max-width: 1200px) {
    bottom: 14%;
  }
  // @media screen and (min-width: 1800px) {
  //   bottom: 29%;
  // }
}

.timeup-card {
  width: 54rem;
  border: 0px;
  border-radius: 10px;

  @media screen and (min-width: 1500px) {
    width: 70rem;
  }
  @media screen and (max-width: 768px) {
    width: 40rem;
  }
  @media screen and (max-width: 600px) {
    width: 32rem;
  }
}

.psg-overlay {
  @media screen and (max-width: 600px) {
    position: fixed;
    width: 100%;
    height: 89vh;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 28, 92, 0.7);
    z-index: 999;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.sidenav {
  .quit {
    @media screen and (min-width: 1400px) {
      width: 13.5%;
    }
    @media screen and (min-width: 1600px) {
      width: 12%;
    }
    @media screen and (min-width: 1800px) {
      width: 11%;
    }
  }
}

.pad-for-audio {
  padding-bottom: 6em;
  @media screen {
    padding-bottom: 4em;
  }
}
