.landing-page {
  .landing-hero {
    background-color: #0f1e41;

    .nav-wrapper {
      background-color: #0f1e41;
    }
    .hero-section {
      padding: 3em 0;
      overflow-x: hidden;
      .hero-caption {
        h1 {
          text-align: left;
          font-family: $worksans-bold;
          color: #10a3ff;
        }
        .caption-subtitle {
          max-width: 350px;
          span {
            color: white;
            font-family: $regular-sans;
            font-size: 16px;
          }
        }
      }
      .hero-image {
        img {
          right: 51%;
          position: relative;
          width: 156%;
          bottom: 50px;
        }
      }
    }
  }

  .trusted-section {
    .trusted-wrapper {
      margin-top: 3em;
      padding: 2em 0;
      background-color: rgba(16, 163, 255, 0.1);
      border-radius: 40px;

      .align-right {
        text-align: right;
      }
      img {
        width: 60%;
      }
      .trusted-text {
        padding: 1em;
        h2 {
          font-family: $helvetica-bold;
          color: #0f1e41;
        }
        .trusted-caption {
          margin-top: 2em;
          span {
            color: #0f1e41;
            line-height: 2;
          }
        }
      }
    }
  }
}
